				icon:'none',
			    duration: 2000
			});
		}, 1100);
	},
	onReachBottom(){
		setTimeout(()=>{
			this.status = 'loading'
		}, 500)
		let str = {
					avatar: '../../static/notice/1.jpg',
					name: '程序员',
					time: '2020年4月26日 晚上11:00',
